:root {
  --font-family: "Roboto", sans-serif;
  --second-family: "TTFirsNeue", sans-serif;
  --content-width: 1290px;
  --container-offset: 30px;
  --container-width: calc(var(--content-width) + (var(--container-offset) * 2));
  --offset: 30px;
  --light-color: #fff;
  --background-color: #202020;
  --btn-color: #FF6E30;
  --hover-color: #BC572B;
  --active-color: #CE4307;
  --form-color: #FF3030;
}

/* stylelint-disable */
