  src: url("../fonts/Roboto-Regular.woff2") format("woff2");
  src: url("../fonts/Roboto-Regular.woff") format("woff");
  font-weight: 400;
  font-display: swap;
  font-style: normal;
}
html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  background-color: var(--light-color);
}

.page {
  height: 100%;
  font-family: var(--font-family);
  -webkit-text-size-adjust: 100%;
}

.page__body {
  margin: 0;
  min-width: 320px;
  min-height: 100%;
  font-size: 16px;
}

img {
  height: auto;
  max-width: 100%;
  object-fit: cover;
}

a {
  text-decoration: none;
}

.site-container {
  overflow: hidden;
}

.is-hidden {
  display: none !important;
}

.btn-reset {
  border: none;
  padding: 0;
  background-color: transparent;
  cursor: pointer;
}

.list-reset {
  list-style: none;
  margin: 0;
  padding: 0;
}

.input-reset {
  -webkit-appearance: none;
  appearance: none;
  border: none;
  border-radius: 0;
  background-color: #fff;
}
.input-reset::-webkit-search-decoration, .input-reset::-webkit-search-cancel-button, .input-reset::-webkit-search-results-button, .input-reset::-webkit-search-results-decoration {
  display: none;
}

.visually-hidden {
  position: absolute;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  width: 1px;
  height: 1px;
  clip: rect(0 0 0 0);
}

.container {
  margin: 0 auto;
  padding: 0 var(--container-offset);
  max-width: var(--container-width);
}
@media (max-width: 577px) {
  .container {
    padding: 0 21px;
  }
}

.js-focus-visible :focus:not(.focus-visible) {
  outline: none;
}

.centered {
  text-align: center;
}

.dis-scroll {
  position: fixed;
  left: 0;
  top: 0;
  overflow: hidden;
  width: 100%;
  height: 100vh;
  overscroll-behavior: none;
}

.page--ios .dis-scroll {
  position: relative;
}

.flex {
  display: flex;
}

.grid {
  display: grid;
  column-gap: var(--offset);
  row-gap: var(--offset);
  grid-template-columns: repeat(12, 1fr);
}

.big-card {
  grid-column: 6 span;
}

p, ul, h1, h2, h3, h4 {
  margin: 0;
}

.title-litl {
  margin-bottom: 5px;
  font-family: var(--second-family);
  font-style: normal;
  font-weight: 700;
  font-size: 24px;
  line-height: 31px;
}
@media (max-width: 577px) {
  .title-litl {
    font-size: 18px;
    line-height: 23px;
  }
}

.btn {
  display: inline-flex;
  align-items: center;
  justify-content: center;
  text-align: center;
  font-style: normal;
  font-weight: 700;
  font-size: 16px;
  line-height: 19px;
  border: 1px solid var(--btn-color);
  padding: 15.5px 22px;
  background: var(--btn-color);
  color: var(--light-color);
  transition: background-color 0.3s ease-in-out, border-color 0.3s ease-in-out;
}
@media (max-width: 577px) {
  .btn {
    padding: 12px 21px;
    font-size: 14px;
    line-height: 16px;
  }
}
.btn:focus {
  outline: none;
  outline: 3px solid var(--active-color);
  border-color: var(--active-color);
}
@media (any-hover: hover) {
  .btn:hover {
    background-color: var(--hover-color);
  }
}
.btn:active {
  background-color: var(--active-color);
}
.btn .btn.focus-visible {
  background-color: var(--active-color);
}

.header__bottom {
  padding: 12px 0px;
  margin-bottom: 34px;
  background-color: #4A4A4A;
  display: none;
}
@media (max-width: 992px) {
  .header__bottom {
    display: block;
  }
}
@media (max-width: 577px) {
  .header__bottom {
    margin-bottom: 31px;
  }
}

.burger {
  position: relative;
  width: 20px;
  height: 18px;
  display: none;
}
@media (max-width: 577px) {
  .burger {
    display: block;
  }
}
.burger:focus {
  outline: 1px solid var(--hover-color);
  outline-offset: 5px;
}
.burger__line {
  position: absolute;
  top: 50%;
  left: 50%;
  height: 2px;
  width: 20px;
  background-color: var(--light-color);
  transform: translateX(-50%) translateY(-50%);
  transition: color 0.3s ease-in-out;
}
.burger__line:first-child {
  height: 2px;
  margin-top: -6px;
}
.burger__line:last-child {
  left: 0px;
  width: 10px;
  height: 2px;
  margin-top: 7px;
  transform: translateY(-50%);
}
.burger__line:focus {
  color: var(--btn-color);
}
.burger__line:hover {
  color: var(--hover-color);
}
.burger__line:active {
  color: var(--active-color);
}

.nav__list-burger {
  justify-content: space-between;
}
@media (max-width: 577px) {
  .nav__list-burger {
    flex-direction: column;
    margin-bottom: 212px;
    gap: 30px;
  }
}

.contacts__mobail-burger {
  display: none;
}
@media (max-width: 577px) {
  .contacts__mobail-burger {
    display: block;
  }
}

.nav__close {
  display: none;
  margin-bottom: 16px;
  transition: color 0.3s ease-in-out;
}
@media (max-width: 577px) {
  .nav__close {
    display: block;
  }
}
.nav__close path {
  transition-property: fill, transform;
  transition: color 0.3s ease-in-out;
}
.nav__close:focus-visible {
  outline: none;
  outline: 1px solid var(--active-color);
}
.nav__close:hover:not(:focus-visible) path {
  fill: var(--btn-color);
}
.nav__close:active:not(:focus-visible) path {
  fill: var(--active-color);
}

@media (max-width: 577px) {
  .nav__burger {
    position: absolute;
    top: 55px;
    left: 0;
    bottom: 0;
    background-color: var(--background-color);
    padding: 25px;
    width: 100%;
    height: 630px;
    opacity: 0;
    z-index: 2;
    transform: translateX(-100%);
    visibility: hidden;
    transition: all 0.3s ease-in-out;
  }
}

.nav__burger.active {
  transform: translateX(0);
  opacity: 1;
  visibility: visible;
  z-index: 1000;
}

.header {
  padding: 22.5px 0;
  margin-bottom: 40px;
  background-color: var(--background-color);
}
@media (max-width: 1200px) {
  .header {
    margin-bottom: 33px;
    padding: 24px 0;
  }
}
@media (max-width: 992px) {
  .header {
    margin-bottom: 0px;
  }
}
.header__top {
  width: 100%;
  align-items: center;
}
.header__container {
  position: relative;
  align-items: center;
}
.header__logo-top {
  margin-right: 139px;
}
@media (max-width: 1200px) {
  .header__logo-top {
    max-width: 166px;
    margin-right: 81px;
  }
}
@media (max-width: 577px) {
  .header__logo-top {
    max-width: 112px;
  }
}
@media (max-width: 577px) {
  .header__btn svg {
    width: 20px;
    height: 20px;
  }
}
.header__btn path {
  transition-property: fill, transform;
  transition: 0.3s ease-in-out;
}
.header__btn:focus-visible {
  outline: none;
  outline: 1px solid var(--active-color);
}
.header__btn:hover:not(:focus-visible) path {
  fill: var(--btn-color);
}
.header__btn:active:not(:focus-visible) path {
  fill: var(--active-color);
}
.header__form {
  display: flex;
  max-width: 250px;
  min-height: 80px;
  position: absolute;
  right: 29px;
  opacity: 0;
  top: -100px;
  align-items: center;
  background-color: var(--background-color);
  transition: all 0.5s ease-in-out;
}
@media (max-width: 577px) {
  .header__form {
    width: 92%;
    right: 16px;
  }
}
.header__input {
  width: 100%;
  display: flex;
  margin-right: 5px;
  border: none;
  border-bottom: 1px solid #E9E9E9;
  font-family: var(--second-family);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  align-items: center;
  background-color: var(--background-color);
  color: #E9E9E9;
  transition: color 0.3s ease-in-out;
}
.header__input:focus {
  outline: none;
}
@media (any-hover: hover) {
  .header__input:hover {
    border-bottom: 1px solid var(--btn-color);
  }
}
.header__input:active {
  border-bottom: 1px solid var(--active-color);
}
@media (max-width: 577px) {
  .header__input {
    width: 100%;
  }
}

.nav {
  max-width: 740px;
  align-items: center;
  margin-right: auto;
}
.nav__list {
  gap: 78px;
}
@media (max-width: 1200px) {
  .nav__list {
    gap: 33px;
  }
}
@media (max-width: 992px) {
  .nav__list {
    display: none;
  }
}
.nav__link {
  font-family: var(--second-family);
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 21px;
  color: var(--light-color);
  transition: color 0.3s ease-in-out;
}
.nav__link:focus {
  outline: none;
}
@media (any-hover: hover) {
  .nav__link:hover {
    color: var(--btn-color);
  }
}
.nav__link:active {
  color: var(--active-color);
}

.nav__link.focus-visible {
  outline: 1px solid var(--hover-color);
  outline-offset: 5px var(--hover-color);
  background-color: var(--hover-color);
}

.search__form_show {
  max-width: 250px;
  top: -25px;
  opacity: 1;
}
@media (max-width: 992px) {
  .search__form_show {
    max-width: 343px;
    min-height: 78px;
  }
}
@media (max-width: 577px) {
  .search__form_show {
    width: 92%;
    top: -33px;
    right: 16px;
  }
}

.work {
  padding-bottom: 62px;
}
@media (max-width: 992px) {
  .work {
    padding-bottom: 65px;
  }
}
@media (max-width: 577px) {
  .work {
    padding-bottom: 44px;
  }
}
.work__container {
  display: flex;
}
@media (max-width: 577px) {
  .work__container {
    flex-direction: column;
  }
}
.work__right-content {
  display: flex;
  flex-direction: column;
  width: calc((100% - 30px) / 2);
}
@media (max-width: 577px) {
  .work__right-content {
    width: 100%;
  }
}
.work__pic {
  object-fit: cover;
  width: 100%;
  height: 100%;
}
.work__left-content {
  width: calc((100% - 30px) / 2);
  display: flex;
  flex: 1;
  flex-direction: column;
  position: relative;
  margin-right: 30px;
  padding: 31.5% 4% 4% 5%;
  align-items: flex-start;
  color: var(--light-color);
  background-image: url("../img/fotoapparat.jpg");
  background-position: center;
  background-repeat: no-repeat;
  background-size: cover;
}
@media (max-width: 1200px) {
  .work__left-content {
    padding: 44% 1% 5% 3%;
    background-image: url("../img/fotoapparat-1024.jpg");
  }
}
@media (max-width: 992px) {
  .work__left-content {
    padding: 32% 1% 5% 3%;
    background-image: url("../img/fotoapparatbec-768.jpg");
  }
}
@media (max-width: 577px) {
  .work__left-content {
    width: 100%;
    margin-right: 0px;
    margin-bottom: 35px;
    padding: 84% 14% 10% 11%;
    background-image: url("../img/fotoapparat-1024.jpg");
  }
}
.work__birka {
  position: absolute;
  top: 15px;
  right: 15px;
  padding: 7px 15px;
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 16px;
  color: var(--light-color);
  background: var(--background-color);
  transition: color 0.3s ease-in-out;
}
@media (max-width: 577px) {
  .work__birka {
    top: 5px;
    right: 5px;
    padding: 5px 8px;
    font-size: 12px;
    line-height: 14px;
  }
}
.work__birka:focus {
  outline: none;
  outline: 3px solid var(--active-color);
  border-color: var(--active-color);
}
@media (any-hover: hover) {
  .work__birka:hover {
    color: var(--hover-color);
  }
}
.work__birka:active {
  color: var(--active-color);
}
.work__birka .work__birka.focus-visible {
  color: var(--active-color);
}
.work__descr {
  margin: 0;
  margin-bottom: 5px;
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 19px;
}
@media (max-width: 1200px) {
  .work__descr {
    margin-bottom: 10px;
  }
}
@media (max-width: 577px) {
  .work__descr {
    font-size: 14px;
    line-height: 16px;
  }
}
.work__title {
  margin-top: 0px;
  margin-bottom: 14px;
  font-family: var(--second-family);
  font-style: normal;
  font-weight: 700;
  font-size: 36px;
  line-height: 104%;
}
@media (max-width: 1200px) {
  .work__title {
    font-size: 34px;
    line-height: 100%;
  }
}
@media (max-width: 577px) {
  .work__title {
    font-size: 24px;
    line-height: 110%;
  }
}
.work__title-mini {
