  .footer {
    padding-top: 29px;
    padding-bottom: 0px;
  }
}
@media (max-width: 577px) {
  .footer__list {
    margin-bottom: 33px;
  }
}
.footer__left {
  width: 34%;
}
@media (max-width: 1200px) {
  .footer__left {
    width: 39%;
  }
}
@media (max-width: 577px) {
  .footer__left {
    display: none;
  }
}
.footer__logo {
  margin-bottom: 5px;
}
@media (max-width: 1200px) {
  .footer__logo {
    max-width: 166px;
  }
}
.footer__span {
  font-family: var(--second-family);
  font-style: normal;
  font-weight: 400;
  font-size: 14px;
  line-height: 18px;
  display: flex;
  align-items: center;
  color: #e9e9e9;
}
.footer__center {
  width: 29%;
}
@media (max-width: 1200px) {
  .footer__center {
    width: 39%;
  }
}
@media (max-width: 992px) {
  .footer__center {
    display: none;
  }
}
.footer__description {
  font-style: normal;
  font-weight: 400;
  font-size: 16px;
  line-height: 22px;
  color: #CACACA;
}
.footer__right {
  width: 13%;
  flex-direction: column;
  align-items: center;
  margin-left: auto;
}
@media (max-width: 1440px) {
  .footer__right {
    width: 17%;
  }
}
@media (max-width: 992px) {
  .footer__right {
    width: 23%;
  }
}
@media (max-width: 577px) {
  .footer__right {
    width: 100%;
  }
}
.footer__biginteres {
  margin-bottom: 8px;
}
.footer__social-link {
  display: inline-block;
  transition: color 0.3s ease-in-out;
}

.footer__social-link:not(:last-child) {
  margin-right: 20px;
}

.footer__social-link path {
  transition-property: fill, transform;
  transition: 0.3s ease-in-out;
}

.footer__social-link:focus-visible {
  outline: none;
  outline: 1px solid var(--active-color);
}

.footer__social-link:hover:not(:focus-visible) path {
  fill: var(--btn-color);
}

.footer__social-link:active:not(:focus-visible) path {
  fill: var(--active-color);
  opacity: 0.4;
}

/*footer-bottom*/
.footer__bottom {
  display: none;
  min-height: 62px;
  padding: 14px 74px;
  background-color: #4A4A4A;
}
@media (max-width: 577px) {
  .footer__bottom {
    display: flex;
    flex-direction: column;
    align-items: center;
    text-align: center;
  }
}

.footer__span-bottom {
  font-weight: 400;
  font-size: 10px;
  line-height: 13px;
  color: #E9E9E9;
}

.footer__logo-bottom {
  max-width: 112px;
}
